<script>

import {Swiper, SwiperSlide} from 'vue-awesome-swiper';
import 'swiper/swiper-bundle.css';

export default {
  components: {
    Swiper,
    SwiperSlide
  },
  data() {
    return {
      model: {
        id: null,
        title: null,
        description: null,
        membersCount: null,
        website: null,
        administrators: null,
        facebook: null,
        email: null,
        instagram: null,
        youtube: null,
        photo: null
      },
      form: {
        firstName: '',
        lastName: '',
        email: '',
        message: ''
      },
      swiperOptions: {
        loop: true,
        navigation: {
          nextEl: '.swiper-button-next',
          prevEl: '.swiper-button-prev'
        },
        pagination: {
          el: '.swiper-pagination',
          type: 'bullets',
          clickable: true
        },
        breakpoints: {
          320: {
            slidesPerView: 1,
          },
          790: {
            slidesPerView: 2,
          },
          1100: {
            slidesPerView: 3,
          }
        }
      },
      formSend: false,
      pageError: false,
      showFullDescription: false,
      administratorsString: ''
    }
  },
  methods: {
    setAdministratorsSting(administrators) {
      const adminsCount = administrators.length;
      let i = 0;
      administrators.map(admin => {
        i++;
        const add = (adminsCount === i) ? '' : ', ';
        this.administratorsString += admin.title + add;
      });
    },
    loadPublicGroup() {
      console.log("loadPublicGroup")
      let url = ['PUBLIC_GROUP_GET_INFO', this.$route.params.id];
      this.makeRequest(url, 'GET', null, this.$route.params.id)
          .then((res) => {
            if (res.status === 200) {
              this.model = {...res.data};
              this.setAdministratorsSting(res.data.administrators);
            }
          })

      const data = {
        userEmail: 'testemail@test.com',
        firstName: '',
        lastName: '',
        description: '',
        code: this.$route.params.code,
        groupId: this.$route.params.id,
        isValidateOnly: true,
      }

      this.makeRequest('GROUP_JOIN', 'POST', data)
          .then((res) => {
            if (res.status !== 200) {
              this.pageError = true;
            }
          })
          .catch(() => {
            this.pageError = true;
          });
    },
    joinPublicGroup() {

      const data = {
        userEmail: this.form.email,
        firstName: this.form.firstName,
        lastName: this.form.lastName,
        description: this.form.message ? this.form.message : '',
        code: this.$route.params.code,
        groupId: this.$route.params.id,
        isValidateOnly: false,
      }

      this.makeAnonRequest('GROUP_JOIN', 'POST', data)
          .then(res => {
            if (res.status === 200) {
              this.formSend = true;


            }
          }).catch(error => {
        console.log('error', error);
      });

    },
    scrollToForm() {
      const element = document.getElementById('joinform');
      element.scrollIntoView({behavior: "smooth"});
    },
  },
  mounted() {
    // LOAD GROUP
    this.loadPublicGroup();
  }
}
</script>

